.usage-info--sidebar {
  min-width: 175px;
  padding: 15px 30px 12px 30px;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  transition: padding 0.3s ease-in-out; }
  .usage-info--sidebar:after {
